import React, { useState, useEffect } from "react";
import {
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions,
  Button, 
  TextField, 
  MenuItem, 
  InputAdornment, 
  IconButton,
  Box,
  Typography,
  Chip,
  Menu,
  MenuItem as MuiMenuItem
} from "@mui/material";
import PhotoCamera from "@mui/icons-material/PhotoCamera";
import CameraAlt from "@mui/icons-material/CameraAlt";
import Image from "@mui/icons-material/Image";
import { getPhoto } from "../utils/camera";

const exerciseTypes = [
  { value: "running", label: "", color: "#FF5722" },
  { value: "cycling", label: "", color: "#4CAF50" },
  { value: "gym", label: "", color: "#9C27B0" },
  { value: "badminton", label: "", color: "#795548" },
  { value: "other", label: "", color: "#9E9E9E" },
];

export default function ExerciseForm({ open, onClose, onSubmit, defaultDate, editingExercise }) {
  const [form, setForm] = useState({
    exerciseType: "running",
    name: "",
    duration: "",
    calories: "",
    time: new Date().toLocaleTimeString().slice(0, 5),
    note: "",
    image: "",
    date: defaultDate.toISOString().slice(0, 10),
  });
  const [imageMenuAnchor, setImageMenuAnchor] = useState(null);

  // 
  useEffect(() => {
    if (editingExercise) {
      setForm({
        exerciseType: editingExercise.exerciseType,
        name: editingExercise.name,
        duration: editingExercise.duration,
        calories: editingExercise.calories,
        time: editingExercise.time,
        note: editingExercise.note || "",
        image: editingExercise.image || "",
        date: editingExercise.date,
      });
    } else {
      // 
      setForm({
        exerciseType: "running",
        name: "",
        duration: "",
        calories: "",
        time: new Date().toLocaleTimeString().slice(0, 5),
        note: "",
        image: "",
        date: defaultDate.toISOString().slice(0, 10),
      });
    }
  }, [editingExercise, defaultDate]);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handlePhoto = async (sourceType = 'both') => {
    try {
      const img = await getPhoto(sourceType);
      if (img) setForm({ ...form, image: img });
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleImageMenuOpen = (event) => {
    setImageMenuAnchor(event.currentTarget);
  };

  const handleImageMenuClose = () => {
    setImageMenuAnchor(null);
  };

  const handleSubmit = () => {
    if (!form.name.trim()) {
      alert('');
      return;
    }
    onSubmit(form);
    setForm({
      exerciseType: "running",
      name: "",
      duration: "",
      calories: "",
      time: new Date().toLocaleTimeString().slice(0, 5),
      note: "",
      image: "",
      date: defaultDate.toISOString().slice(0, 10),
    });
  };

  const selectedExerciseType = exerciseTypes.find(type => type.value === form.exerciseType);

  return (
    <Dialog 
      open={open} 
      onClose={onClose} 
      fullWidth 
      maxWidth="sm"
      PaperProps={{
        sx: { borderRadius: 2 }
      }}
    >
      <DialogTitle sx={{ pb: 1 }}>
        <Typography variant="h6" sx={{ fontWeight: 600 }}>
          {editingExercise ? '' : ''}
        </Typography>
      </DialogTitle>
      <DialogContent>
        <Box sx={{ mb: 3 }}>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
            
          </Typography>
          <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
            {exerciseTypes.map((type) => (
              <Chip
                key={type.value}
                label={type.label}
                onClick={() => setForm({ ...form, exerciseType: type.value })}
                sx={{
                  backgroundColor: form.exerciseType === type.value ? type.color : 'transparent',
                  color: form.exerciseType === type.value ? 'white' : 'text.primary',
                  border: `1px solid ${type.color}`,
                  '&:hover': {
                    backgroundColor: type.color,
                    color: 'white'
                  }
                }}
              />
            ))}
          </Box>
        </Box>

        <TextField
          fullWidth 
          margin="normal" 
          label=" *" 
          name="name"
          value={form.name} 
          onChange={handleChange}
          variant="outlined"
          sx={{ mb: 2 }}
        />

        <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
          <TextField
            fullWidth 
            margin="normal" 
            label="" 
            name="duration"
            value={form.duration} 
            onChange={handleChange}
            placeholder="30"
            variant="outlined"
          />
          <TextField
            fullWidth 
            margin="normal" 
            label="" 
            name="calories"
            value={form.calories} 
            onChange={handleChange}
            placeholder="200"
            variant="outlined"
            InputProps={{
              endAdornment: <InputAdornment position="end">kcal</InputAdornment>,
            }}
          />
        </Box>

        <TextField
          fullWidth 
          margin="normal" 
          label="" 
          name="time"
          value={form.time} 
          onChange={handleChange} 
          type="time"
          InputLabelProps={{ shrink: true }}
          variant="outlined"
          sx={{ mb: 2 }}
        />

        <TextField
          fullWidth 
          margin="normal" 
          label="" 
          name="note"
          value={form.note} 
          onChange={handleChange}
          multiline
          rows={2}
          variant="outlined"
          sx={{ mb: 2 }}
        />

        <Box sx={{ mb: 2 }}>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
            
          </Typography>
          <Box sx={{ 
            border: '2px dashed #ddd', 
            borderRadius: 2, 
            p: 2, 
            textAlign: 'center',
            cursor: 'pointer',
            '&:hover': {
              borderColor: 'primary.main',
              backgroundColor: 'action.hover'
            }
          }} onClick={handleImageMenuOpen}>
            {form.image ? (
              <Box>
                <img 
                  src={form.image} 
                  alt="exercise" 
                  style={{ 
                    width: '100%', 
                    maxHeight: 200, 
                    borderRadius: 8,
                    objectFit: 'cover'
                  }} 
                />
                <Typography variant="body2" color="primary" sx={{ mt: 1 }}>
                  
                </Typography>
              </Box>
            ) : (
              <Box>
                <PhotoCamera sx={{ fontSize: 48, color: 'text.secondary', mb: 1 }} />
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </Box>
            )}
          </Box>
        </Box>
      </DialogContent>
      <DialogActions sx={{ p: 3, pt: 1 }}>
        <Button onClick={onClose} variant="outlined">
          
        </Button>
        <Button 
          onClick={handleSubmit} 
          variant="contained" 
          disabled={!form.name.trim()}
          sx={{ 
            minWidth: 80,
            backgroundColor: selectedExerciseType?.color,
            '&:hover': {
              backgroundColor: selectedExerciseType?.color,
              opacity: 0.8
            }
          }}
        >
          {editingExercise ? '' : ''}
        </Button>
      </DialogActions>

      {/*  */}
      <Menu
        anchorEl={imageMenuAnchor}
        open={Boolean(imageMenuAnchor)}
        onClose={handleImageMenuClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <MuiMenuItem onClick={() => {
          handlePhoto('both');
          handleImageMenuClose();
        }}>
          <CameraAlt sx={{ mr: 1 }} />
          
        </MuiMenuItem>
        <MuiMenuItem onClick={() => {
          handlePhoto('camera');
          handleImageMenuClose();
        }}>
          <CameraAlt sx={{ mr: 1 }} />
          
        </MuiMenuItem>
        <MuiMenuItem onClick={() => {
          handlePhoto('gallery');
          handleImageMenuClose();
        }}>
          <Image sx={{ mr: 1 }} />
          
        </MuiMenuItem>
      </Menu>
    </Dialog>
  );
} 